import React, { Component } from 'react';
import './App.css';
import Article from './Article';

class News extends Component {
  render() {
    const {data} = this.props;
    const newsTemplate = data.map((item)=>{
      return <Article key={item.id} data={item}/>
  })
  return (
    <div>
      {newsTemplate}
    </div>
    )

  }
}

export default News;



