import React from 'react';
import './TestInput.css';

class TestInpyt extends React.Component{
    constructor(props){
        super(props);
        this.state={
            inputValue:'',
            textareaValue:'',
            checkboxValue: false
        }
    }

    onInputHandler = (e) => {
        this.setState({
            inputValue:e.currentTarget.value
        })
    }

    onTextareaHandler = (e) => {
        this.setState({
            textareaValue:e.currentTarget.value
        })
    }

    onChangeCheckbox = (e) => {
        this.setState({
            checkboxValue:e.currentTarget.checked
        })
    }

    onBtnHandler = (e) => {
        e.preventDefault();
        const{inputValue, textareaValue} = this.state

        this.props.onAddNews({ inputValue, textareaValue })
        this.props.onAddNews({
            id: +new Date(), //  id      1  1970     UTC
            author: inputValue, // name    author
            text:textareaValue
        });
    }

    validate = () => {
        const { inputValue, textareaValue, checkboxValue } = this.state;
        if (inputValue.trim() && textareaValue.trim() && checkboxValue) {
        return true;
        }
        return false;
    }
    render(){
        const{inputValue, textareaValue, checkboxValue} = this.state; //    
        return(
           <form className='add'>
               <input
                    type='text'
                    className='add__author'
                    placeholder=' '
                    onChange={this.onInputHandler}
                    value={inputValue}
               />
                <textarea
                    type='text'
                    className='add__text'
                    placeholder=' '
                    onChange={this.onTextareaHandler}
                    value={textareaValue}
               />
                <label className='add__checkrule'>
                    <input type='checkbox' onChange={this.onChangeCheckbox} value={checkboxValue}/>   
                </label>
                <button
                    className='add_btn'
                    onClick={this.onBtnHandler}
                    disabled={!this.validate()}
                >
                      
                </button>  
           </form>
        )
    }
}
export default TestInpyt;


